.buy{
  .catalog__top2{
    margin-top: 41px;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 20px));
    gap: 38px 30px;
    margin-top: 30px;
  }
  .product-card{
    padding: 0 30px;
  }
}

.banner{
  grid-column: 1/-1;
  padding: 55px 15px 76px;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)), url(../img/banner.webp);
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
  text-align: center;
  &__desc{
    font-size: 22px;
    line-height: 150%;
    letter-spacing: 0.1em;
    margin-top: 5px;
  }
  &__btn{
    margin-top: 23px;
    height: 50px;
    padding: 0 42px;
    border: 1px solid #FFFFFF;
    color: #fff;
    font-size: 16px;
    &:hover{
      color: $black;
      background-color: #fff;
    }
  }
}


@media screen and (max-width: 992px){
  .buy {
    .catalog__top2 {
      margin-top: 22px;
    }
    &__items{
      margin-top: 16px;
      grid-template-columns: repeat(3,calc(100% / 3 - (44px / 3)));
      gap: 30px 22px;
    }
    .product-card{
      padding: 0;
    }
  }
}


@media screen and (max-width: 740px){
  .buy{
    &__items{
      grid-template-columns: repeat(2,calc(50% - 10px));
      gap: 25px 20px;
    }

    .banner{
      grid-row: 3/4;
    }
  }

  .banner{
    padding: 70px 20px;
    &__desc{
      margin-top: 17px;
    }
    &__btn{
      min-width: 227px;
    }
  }
}
