.range-slider{
  &__inputs{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 50px;
    position: relative;
    margin-bottom: 29px;
    &:before{
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      width: 14px;
      height: 1px;
      background-color: $black;
    }
  }
  &.noUi-horizontal{
    height: 6px;
    box-shadow: none;
    border: none;
    width: calc(100% - 20px);
    margin-left: 5px;
  }
  .noUi-connect{
    background: #D9D9D9;
    border-radius: 16px;
  }
  .noUi-handle{
    top: -8px;
    background: $black;
    border: 5px solid #FFFFFF;
    box-shadow: 0px 1px 9px rgba(0, 0, 0, 0.25);
    width: 22px;
    height: 22px;
    border-radius: 50%;
    &:before,
    &:after{
      display: none;
    }
  }
}

.fg {
  position: relative;
  margin: 0;
  input ,
  textarea{
    border: $border;
    padding: 0 21px;
    height: 50px;
    width: 100%;
    font-weight: 300;
    font-size: 14px;
    &::placeholder {
      color: #5E5E5E;
    }
    &:focus {
      border-color: $black
    }
  }
  textarea{
    height: 233px;
    resize: none;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .iti{
    width: 100%;
  }
}

.mcheck,
.mradio{
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        background-repeat: no-repeat;
        background-position: center;
      }
    }
  }
  label {
    display: block;
    padding-left: 32px;
    position: relative;
    min-height: 22px;
    font-size: 14px;
    line-height: 22px;
    font-weight: 300;
    letter-spacing: 0.1em;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border: 1px solid $black;

    }
  }
}

.mcheck{
  input{
    &:checked+label {
      &::before {
        background-image: url(../img/check.svg);
      }
    }
  }
  label{
    &:before{
      width: 20px;
      height: 20px;
      left: 2px;
      top: 2px;
    }
  }
}

.mradio{
  input{
    &:checked+label {
      &::before {
        background-image: url(../img/radio.svg);
      }
    }
  }
  label{
    padding-left: 23px;
    &:before{
      width: 12.58px;
      height: 12.58px;
      left: 1.2px;
      top: 50%;
      transform: translateY(-50%);
      border-radius: 50%;
    }
  }
}

.m-select{
  width: unset;
  height: unset;
  border: none;
  float: unset;
  font-size: 16px;
  line-height: 175%;
  padding: 0;
  .current{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    min-width: 88px;
    background-image: url(../img/arrow.svg);
    background-repeat: no-repeat;
    background-position: right center;
    padding-right: 30px;
  }
  .list{
    background: #FFFFFF;
    box-shadow: 0px 4px 17px rgba(0, 0, 0, 0.25);
    border: none;
    border-radius: 0;
  }
  .option{
    padding: 0 16px;
    height: 47px;
    display: flex;
    align-items: center;
    font-size: 16px;
    &:not(:last-child){
      border-bottom: $border;
    }
    &.selected{
      color: #5E5E5E;
      font-weight: 400;
      background-color: transparent !important;
    }
  }
}
