.catalog-item{
  padding-bottom: 140px;
  &__grid{
    display: grid;
    grid-template-columns: 57.5% calc(42.5% - 30px);
    gap: 30px;
    margin-top: 60px;
  }
  &__left{

  }
  &__info{
    display: flex;
    justify-content: space-between;
    margin-top: 27px;
  }
  &__btn{
    width: 180px;
    height: 60px;
    flex-shrink: 0;
  }
  &__art{
    font-size: 16px;
    line-height: 175%;
    margin-bottom: 4px;
  }
  &__desc{
    margin-top: 32px;
    font-size: 14px;
    line-height: 214%;
    color: #5E5E5E;
  }
  &__shops{
    margin-top: 16px;
    width: 360px;
    max-width: 100%;
    .catalog-item__btn{
      width: 100%;
    }
  }
  &__shop{
    width: 100%;
    height: 71px;
    margin-top: 16px;
    font-size: 16px;
    &:not(:last-child){
      margin-bottom: 20px;
    }
    img{
      margin-left: 10px;
    }
  }
}

.gallery{
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  &__wrap{
    position: relative;
    padding-bottom: 86.4%;
  }
  img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__big{
    width: 71%;
  }
  &__small{
    width: 25.905%;
    .swiper-slide{

    }
    .swiper{
      height: calc(100% - 150px);
    }
  }
  .swiper{
    max-width: 100%;
    max-height: 100%;
    overflow: hidden;
    &-slide{
      padding: 9px;
      border: $border;
    }
    &-button{
      &-next,
      &-prev{
        position: static;
        margin: 0;
        height: unset;
      }
      &-next{
        margin-top: 2px;
      }
    }
  }
  &__img{
    position: relative;
    padding-bottom: 120%;
    height: 100%;
    display: block;
    img{
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-top: 10px;
  }
}

.modification{
  margin-top: 26px;
  &__slider{
    margin-top: 20px;
    display: grid;
    grid-template-columns: 32px calc(100% - 84px) 32px;
    align-items: center;
    gap: 10px;
  }
  &__img{
    position: relative;
    height: 100%;
    padding-bottom: 125%;
    display: block;
    img{
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      object-fit: cover;
    }
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-slide{
      padding: 5px;
      border: $border;
    }
    &-button{
      &-prev,
      &-next{
        margin: 0;
        position: static;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    padding: 30px 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 10px));
      gap: 20px;
      margin: 0;
    }
    &__title{
      font-size: 24px;
    }
    &__info{
      margin-top: 18px;
    }
    &__btn{
      width: 150px;
    }
    &__desc{
      margin-top: 22px;
    }
    &__shops{
      margin-top: 9px;
    }
    &__shop{
      @media screen and (min-width: 700px){
        height: 58px;
        margin-top: 13px;
        &:not(:last-child){
          margin-bottom: 16px;
        }
      }
    }
  }

  .modification{
    &__slider{
      margin-top: 10px;
    }
  }

  .gallery{
    flex-direction: column-reverse;
    height: unset;
    max-height: 100%;
    &__wrap{
      min-height: 100%;
    }
    &__nav{
      display: none;
    }
    &__big{
      width: 100%;
      .swiper{
        &-slide{
          padding: 6px;
        }
      }
    }
    &__small{
      height: unset;
      width: 100%;
      margin-top: 19px;
      padding: 0 20px;
      .swiper{
        height: unset;
        &-slide{
          width: 102px;
          padding: 0;
          border: none;
        }
      }
    }
    &__img{
      padding-bottom: 123.57%;
    }
  }
}


@media screen and (max-width: 700px){
  .catalog-item{
    padding: 0 0 22px;
    overflow: hidden;
    &__grid{
      grid-template-columns: 100%;
      gap: 30px;
    }
    &__desc{
      margin-top: 32px;
    }
    &__shops{
      margin-top: 12px;
    }
  }

  .gallery{
    position: static;
    height: unset;
    width: calc(100% + 40px);
    margin: -1px 0 0 -20px;
    &__wrap{
      position: static;
      padding: 0;
    }
    &__big{
      .swiper{
        border: $border;
        &-slide{
          border: none;
        }
      }
    }
    &__small{
      .swiper{
        overflow: unset;
        &-slide{
          width: 110px;
        }
      }
    }
  }

  .modification{
    &__slider{
      width: calc(100% + 40px);
      margin-left: -20px;
      grid-template-columns: 32px calc(100% - 72px) 32px;
      gap: 4px;
    }
  }
}
