@import '../vars';
@import '../mixins';
.modal {
    padding: 40px 0;
    background: rgba(#000, .7);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    display: flex;
    z-index: 1000;
    opacity: 0;
    pointer-events: none;
    &--active{
        opacity: 1;
        pointer-events: unset;
    }
    &__dialog{
        margin: auto;
        background-color: #fff;
        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
        width: 400px;
        max-width: 100%;
        padding: 32px 43px;
        position: relative;
    }
    &__close{
        position: absolute;
        right: 0;
        top: 0;
        transform: translate(50%,-50%);
        background: #F2F2F2;
        border: 2px solid #FFFFFF;
        box-shadow: 0px 5px 20px rgba(0, 0, 0, 0.25);
        border-radius: 50%;
        width: 36px;
        height: 36px;
    }
    &__form{
        margin-top: 16px;
        .fg{
            margin-bottom: 16px;
        }
    }
    &__btn{
        margin: 26px auto 0;
        width: 180px;
        height: 40px;
    }
}

.modal-product{
    display: grid;
    grid-template-columns: 81px auto;
    gap: 27px;
    margin-top: 24px;
    &__img{
        padding: 2px;
        border: $border;
        height: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
        img{
            max-width: 100%;
            max-height: 100%;
        }
    }
    &__title{
        font-size: 16px;
        line-height: 175%;
    }
    &__price{
        font-size: 24px;
        line-height: 120%;
        letter-spacing: 0.1em;
        margin-top: 15px;
    }
}

.pay-way{
    margin-top: 18px;
    &__title{
        font-weight: 400;
        text-transform: uppercase;
    }
    &__item{
        margin-top: 12px;
    }
}


.thank-modal{
    text-align: center;
    .modal{
        &__dialog{
            width: 350px;
            padding: 48px 20px 35px;
        }
        &__title{
            margin-top: 26px;
        }
        &__desc{
            margin-top: 20px;
            font-size: 16px;
            line-height: 175%;
            color: #5E5E5E;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        padding: 0;
        &__close{
            background: #DBD3CE;
            right: 5px;
            top: 5px;
            transform: unset;
        }
        &__dialog{
            padding: 50px 20px 40px;
        }
    }
}

