.catalog{
  &__grid{
    display: grid;
    grid-template-columns: 330px calc(100% - 360px);
    gap: 30px;
    margin-top: 60px;
  }
  &__filter-btn{
    display: none;
  }
  &__top,
  &__top2{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__count{
    font-size: 16px;
    line-height: 175%;
    color: #5E5E5E;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 20px));
    gap: 70px 30px;
    margin-top: 28px;
  }
  .product-card{
    &__img{
      padding-bottom: 115.16%;
    }
    .body-text{
      line-height: 175%;
    }
  }
  &__more{
    display: block;
    text-align: center;
    font-size: 16px;
    line-height: 175%;
    letter-spacing: 0.1em;
    text-decoration-line: underline;
    margin: 79px auto 0;
  }
}

.filter{
  position: sticky;
  top: 20px;
  &__nav{
    display: none;
  }
  &__group{
    border-top: $border;
  }
  &__top{
    padding: 18px 15px;
    font-size: 18px;
    line-height: 150%;
    text-align: center;
    letter-spacing: 0.1em;
    position: relative;
    cursor: pointer;
    &--active{
      .filter__toggle{
        &:after{
          opacity: 1;
        }
      }
    }
  }
  &__toggle{
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(-4px,-50%);
    width: 14px;
    height: 14px;
    &:before,
    &:after{
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      background-color: $black;
    }
    &:before{
      width: 100%;
      height: 1px;
    }
    &:after{
      height: 100%;
      width: 1px;
      opacity: 0;
    }
  }
  &__bottom{
    padding: 9px 0 32px;
  }
  &__check{
    &:not(:last-child){
      margin-bottom: 18px;
    }
  }
  &__reset{
    margin-top: 5px;
    height: 70px;
    width: 100%;
  }
}

.colors{
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 20px 22px;
  width: 238px;
  max-width: 100%;
  margin: 0 auto;
}

.color{
  position: relative;
  label{
    width: 30px;
    height: 30px;
    display: block;
    border-radius: 50%;
    border: $border;
  }
  input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
    opacity: 0;
    &:checked+label{
      border-color: rgba($black,.35);
      box-shadow: 0 0 0 .25rem rgba($black,.35);
    }
  }
}

.sort{
  display: flex;
  align-items: center;
  &__title{
    font-size: 16px;
    line-height: 175%;
    color: #5E5E5E;
    margin-right: 3px;
  }
  .list{
    left: unset;
    right: 0;
    min-width: 200px;
  }
}


@media screen and (max-width: 1250px){
  .catalog{
    &__items{
      grid-template-columns: repeat(2,calc(50% - 10px));
      gap: 40px 30px;
    }
  }
}


@media screen and (max-width: 992px){
  .catalog{
    padding: 27px 0 21px;
    &__top{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &__filter-btn{
      display: flex;
      width: 225px;
      height: 60px;
      font-size: 16px;
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 20px;
      gap: 0;
    }
    &__items{
      margin-top: 8px;
      grid-template-columns: repeat(3,calc(100% / 3 - (44px / 3)));
      gap: 30px 22px;
    }
    &__more{
      margin-top: 30px;
    }
  }

  .filter{
    padding-bottom: 30px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 30;
    background-color: #fff;
    overflow-y: auto;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &._active{
      opacity: 1;
      pointer-events: unset;
    }
    &__top{
      padding-left: 35px;
      padding-right: 35px;
    }
    &__toggle{
      right: 20px;
    }
    &__bottom{
      padding-left: 20px;
      padding-right: 20px;
    }
    &__reset{
      width: calc(100% - 30px);
      margin-left: auto;
      margin-right: auto;
    }
    &__nav{
      display: flex;
      align-items: center;
      justify-content: center;
      height: 60px;
      border-bottom: 1px solid $black;
      position: sticky;
      top: 0;
      z-index: 3;
      background-color: #fff;
    }
    &__close{
      position: absolute;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}


@media screen and (max-width: 740px){
  .catalog{
    &__top{
      flex-direction: column;
      align-items: center;
    }
    &__title{
      font-size: 24px;
      text-align: center;
    }
    &__filter-btn{
      width: 100%;
      margin-top: 20px;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - 10px));
      gap: 25px 20px;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    &__grid{
      margin-top: 30px;
    }
    &__top2{
      display: block;
    }
    &__count{
      margin-top: 8px;
    }
    &__items{
      margin-top: 18px;
    }
    &__more{
      margin-top: 37px;
    }
  }
}
