.main{
  overflow: hidden;
  position: relative;
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      max-height: 720px;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: block;
      }
    }
    &-pagination{
      bottom: 38px;
      &-bullet{
        background: #D9D9D9;
        border: 2px solid #D9D9D9;
        opacity: 0.5;
        width: 12px;
        height: 12px;
        margin: 0 8px !important;
        &-active{
          border-color: #fff;
          background-color: transparent;
          opacity: 1;
        }
      }
    }
  }
  &__img{
    height: 100%;
  }
}

.new{
  padding: 23px 0 53px;
  &__desc{
    margin-top: 13.58px;
  }
  &__inner{
    margin-top: 90px;
    display: grid;
    grid-template-columns: 40.427% calc(60% - 30px);
    gap: 30px;
  }
  .new-card{
    padding-bottom: 114.04%;
    &__title{
      min-height: 109px;
    }
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 22px));
    gap: 44px;
  }
}

.about{
  padding: 53px 0 43px;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 59.646% - 30px) 59.646%;
    gap: 17px 30px;
  }
  &__content{
    padding-top: 22.3%;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__desc{
    margin-top: 63px;
    font-size: 16px;
    line-height: 175%;
    color: #5E5E5E;
    max-width: 500px;
  }
  &__link{
    margin-top: 23px;

  }
}

.about-info{
  width: 1230px;
  max-width: 100%;
  margin: 90px auto 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__item{
    font-weight: 300;
    font-size: 18px;
    line-height: 133%;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    margin: 0 9px;
    flex-shrink: 0;
  }
  &__line{
    display: block;
    width: 53.02px;
    height: 1px;
    background-color: #DBD3CE;
  }
}

.collection{
  padding: 43px 0 36px;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 15px));
    gap: 30px;
  }
  &__item{
    &:nth-child(even){
      padding-top: 17.4%;
    }
  }
  &__title{
    display: flex;
    align-items: center;
    svg{
      margin-left: 8px;
    }
  }
  &__img{
    margin-top: 18px;
    position: relative;
    padding-bottom: 100%;
    img{
      position: absolute;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.blog{
  padding: 36px 0 58px;
  overflow: hidden;
  &__slider{
    margin-top: 34px;
  }
}


@media screen and (max-width: 992px){
  .main{
    &__img{
      position: relative;
      padding-bottom: 47%;
      img{
        top: 0;
        left: 0;
        position: absolute;
      }
    }
    .swiper{
      &-pagination{
        display: none;
      }
    }
  }

  .new{
    padding: 15px 0 30px;
    &__inner{
      margin-top: 60px;
      grid-template-columns: 32.282% calc(100% - 32.282% - 15px);
      gap: 20px 15px;
    }
    .new-card{
      padding-bottom: 169%;
      &__title{
        min-height: 70px;
      }
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - 11.5px));
      gap: 21px;
    }
  }

  .about{
    padding: 30px 0;
    &__inner{
      grid-template-columns: calc(100% - 52% - 30px) 51.7%;
    }
    &__content{
      padding: 0;
    }
    &__img{
      padding-top: 12.49%;
    }
    &__desc{
      margin-top: 40px;
      @media screen and (min-width: 576px){
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp:7;
        -webkit-box-orient: vertical;
      }
    }
    &__link{
      margin-top: 30px;
    }
  }

  .about-info{
    margin-top: 60px;
    &__item{
      font-size: 16px;
      line-height: 150%;
    }
    &__line{
      width: 29.55px;
      &:first-child,
      &:last-child{
        display: none;
      }
    }
  }

  .collection{
    padding: 30px 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 10px));
      gap: 34px 20px;
    }
    &__item{
      &:nth-child(even){
        padding-top: 13%;
      }
    }
    &__title{
      font-size: 22px;
    }
    &__img{
      margin-top: 12px;
    }
  }

  .blog{
    padding: 30px 0;
    &__slider{
      margin-top: 24px;
    }
    .swiper{
      &-slide{
        width: 315px;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__img{
      padding-bottom: 100%;
    }
  }

  .new{
    &__inner{
      grid-template-columns: 100%;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - 6.5px));
      gap: 13px;
    }
    .new-card{
      padding-bottom: 56.25%;
    }
  }

  .about{
    &__inner{
      grid-template-columns: 100%;
    }
    &__desc{
      margin-top: 22px;
    }
    &__link{
      margin-top: 34px;
    }
    &__img{
      padding: 0;
      width: calc(100% + 40px);
      margin-left: -20px;
    }
  }

  .about-info{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 47px 5px;
    position: relative;
    &__item{
      min-height: 53px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0;
    }
    &__line{
      position: absolute;
      &:nth-child(1),
      &:nth-child(3){
        display: block;
        left: 50%;
        transform: translateX(-50%);
        width: 1px;
        height: 53px;
      }
      &:nth-child(1){
        top: 0;
      }
      &:nth-child(3){
        bottom: 0;
      }
      &:nth-child(5),
      &:nth-child(7){
        width: 53px;
        top: 50%;
        left: 25%;
        transform: translate(-50%,-50%);
      }
      &:nth-child(7){
        left: unset;
        right: 25%;
        transform: translate(50%,-50%);
      }
    }
  }

  .collection{
    &__grid{
      grid-template-columns: 100%;
    }
    &__item{
      &:nth-child(even){
        padding-top: 0;
      }
    }
    &__img{
      margin-top: 8px;
    }
  }

  .blog{
    .swiper{
      &-slide{
        width: 280px;
      }
    }
  }
}
