.consult{
  background: #F5F5F5;
  padding: 40px 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__title{
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    font-family: $font2;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    max-width: 674px;
  }
  &__form{
    display: flex;
    align-items: flex-end;
    width: 430px;
    flex-shrink: 0;
    max-width: 100%;
    margin-left: 20px;
    input{
      flex-grow: 1;
      border: none;
      border-bottom: 1px solid $black;
      height: 49px;
      font-weight: 300;
      font-size: 14px;
      letter-spacing: 0.1em;
      background-color: transparent;
      &::placeholder{
        color: #5E5E5E;
      }
    }
  }
  &__btn{
    width: 180px;
    height: 40px;
    flex-shrink: 0;
    margin-left: 14px;
  }
}

.footer{
  padding: 58px 0 37px;
  &__inner{
    display: flex;
    justify-content: space-between;
    margin-top: 62px;
  }
  &__logo{
    width: 240px;
    margin: -27px 18px 0 -11px;
  }
  &__left{
    flex-shrink: 0;
    display: flex;
  }
  &__tel{
    display: block;
    white-space: nowrap;
    font-weight: 300;
    font-size: 18px;
    line-height: 133%;
    letter-spacing: 0.1em;
  }
  &__mail{
    display: block;
    font-weight: 400;
    margin-top: 9px;
  }
  &__socials{
    display: flex;
    align-items: center;
    margin-top: 27px;
    &-item{
      flex-shrink: 0;
      &:not(:last-child){
        margin-right: 15px;
      }
    }
  }
  &__right{
    max-width: 200px;
  }
  &__conf{
    display: block;
    margin-bottom: 10px;
  }
  &__conf,
  &__co{
    letter-spacing: 0;
    color: #5E5E5E;
  }
  &__links{
    flex-grow: 1;
    max-width: 590px;
    display: flex;
    justify-content: space-between;
    margin: 0 20px;
    &-group{
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }
  &__link{
    margin: 0 auto 8px 0;
    font-weight: 300;
  }
}


@media screen and (max-width: 1200px){
  .footer{
    &__left{
      display: block;
      text-align: center;
    }
    &__logo{
      width: 229px;
      margin: -13px 0 16px;
    }
    &__tel{
      font-weight: 700;
      font-size: 20px;
      line-height: 130%;
    }
    &__mail{
      margin-top: 7px;
    }
    &__socials{
      justify-content: center;
      margin-top: 12px;
    }
  }
}


@media screen and (max-width: 992px){
  .consult{
    padding: 25px 22px;
    align-items: unset;
    &__title{
      font-size: 16px;
      line-height: 175%;
      font-family: $font1;
      font-weight: 400;
    }
    &__form{
      display: block;
      width: 270px;
      input{
        height: unset;
        width: 100%;
        padding: 3px 0 13px;
      }
    }
    &__btn{
      width: 100%;
      margin: 20px 0 0;
    }
  }

  .footer{
    padding: 30px 0 20px;
    &__inner{
      margin-top: 38px;
    }
    &__right{
      max-width: 156px;
    }
    &__links{
      display: block;
      column-count: 2;
      column-gap: 20px;
      margin: 0 10px;
    }
  }
}


@media screen and (max-width: 650px){
  .consult{
    padding: 20px 22px;
    display: block;
    &__title{
      max-width: 100%;
      font-size: 14px;
      line-height: 150%;
      text-transform: unset;
    }
    &__form{
      width: 100%;
      margin: 13px 0 0;
    }
  }

  .footer{
    &__inner{
      margin-top: 40px;
      display: block;
    }
    &__logo{
      margin: 0 auto 13px;
    }
    &__links{
      text-align: center;
      margin: 39px 0 23px;
    }
    &__link{
      margin-right: 0;
    }
    &__right{
      max-width: 100%;
      text-align: center;
    }
  }
}
