@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.mbtn-black{
    font-weight: 300;
    font-size: 14px;
    letter-spacing: 0.1em;
    color: #fff;
    background-color: $black;
    &:hover{
        background-color: lighten($black, 10%);
        color: #fff;
    }
}

.mbtn-black-outline{
    border: 1px solid $black;
    color: $black;
    font-size: 16px;
    &:hover{
        background-color: $black;
        color: #fff;
    }
}
