.contacts{
  padding-bottom: 100px;
  &__inner{
    display: flex;
    justify-content: space-between;
    margin-top: 70px;
  }
  &__left{
    width: 432px;
    margin-right: 44px;
    flex-shrink: 0;
  }
  &__right{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    width: calc(100% - 480px);
  }
  &__subtitle{
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    font-family: $font2;
    text-transform: uppercase;
    max-width: 415px;
  }
  &__desc{
    font-size: 16px;
    line-height: 175%;
    margin-top: 5px;
    p{
      line-height: inherit;
      &:not(:last-child){
        margin-bottom: 5px;
      }
    }
  }
  &__items{
    margin-top: 25px;
  }
  &__item{
    display: flex;
    font-size: 16px;
    line-height: 175%;
    &:not(:last-child){
      margin-bottom: 21px;
    }
    &-icon{
      flex-shrink: 0;
      margin-right: 10px;
    }
    &-wrap{
      display: flex;
      align-items: center;
      margin-bottom: 10px;
      .contacts__item{
        align-items: center;
        font-size: 24px;
        line-height: 150%;
        margin: 0 19px 0 0 !important;
      }
    }
  }
  &__socials{
    display: flex;
    align-items: center;
    &-item{
      margin-right: 7px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 32px;
      height: 32px;
      border-radius: 50%;
      background: #D9D9D9;
      &:hover{
        background: $black;
      }
    }
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__form{
    &-title{
      font-size: 16px;
      line-height: 175%;
      margin-bottom: 20px;
      max-width: 409px;
    }
    .fg{
      margin-bottom: 16px;
    }
  }
  &__check{
    label{
      color: #363636;
    }
  }
  &__btn{
    width: 180px;
    height: 40px;
    margin-top: 30px;
    max-width: 100%;
  }
}


@media screen and (max-width: 1200px){
  .contacts{
    &__inner{
      display: block;
      margin-top: 57px;
    }
    &__left{
      width: 100%;
      margin: 0;
    }
    &__right{
      gap: 24px;
      width: 100%;
      margin-top: 22px;
    }
    &__items{
      width: calc(50% - 24px);
    }
    &__form{
      margin-top: -180px;
    }
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding: 34px 0 30px;
  }
}


@media screen and (max-width: 740px){
  .contacts{
    padding-bottom: 30px;
    &__inner{
      margin-top: 25px;
    }
    &__right{
      grid-template-columns: 100%;
      gap: 33px;
    }
    &__form{
      margin: 0;
    }
    &__items{
      width: 100%;
    }
  }
}
