.logo{
  display: block;
  img{
    width: 100%;
    display: block;
  }
}

.header{
  z-index: 3;
  position: relative;
  padding-bottom: 18px;
  border-bottom: $border;
  &__inner{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__logo{
    width: 253px;
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
  }
  &__search{
    margin-left: 28px;
    &-toggle{
      display: block;
      width: 24px;
      height: 24px;
      background-image: url(../img/search.svg);
      background-repeat: no-repeat;
      background-position: center;
    }
    &._active{
      .header__search-toggle{
        background-image: url(../img/close.svg);
      }
      .header-search{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__burger{
    display: none;
  }
}


.nav{
  &__list{
    display: flex;
    align-items: center;
  }
  &__item{
    &:not(:last-child){
      margin-right: 12px;
    }
    &--active{
      .nav__show{
        border-color: $black;
      }
      .nav__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__link,
  &__show{
    padding: 6px 17px;
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 16px;
    line-height: 175%;
    letter-spacing: 0.1em;
    position: relative;
    border-bottom: 1px solid transparent;
    transition: all .3s;
    &:hover{
      cursor: pointer;
      border-color: $black;
      color: $black;
    }
  }
  &__hidden{
    z-index: 5;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    left: 0;
    top: 100%;
    width: 100%;
    background: #FFFFFF;
    box-shadow: 0px 21px 26px rgba(0, 0, 0, 0.1);
    padding: 22px 0 32px;
    &-inner{
      display: grid;
      grid-template-columns: calc(100% - 490px) 450px;
      gap: 40px;
    }
  }
  &__links{
    padding: 16px 0;
    display: flex;
    justify-content: space-between;
  }
  &__col{
    min-width: 200px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__sublink{
    margin-right: auto;
    font-size: 16px;
    line-height: 175%;
  }
}

.header-search{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  background: #FFFFFF;
  box-shadow: 0px 21px 26px rgba(0, 0, 0, 0.1);
  padding: 22px 0 32px;
  &__inner{
    max-width: 100%;
    width: 600px;
    margin: 0 auto;
    position: relative;
  }
  input{
    height: 50px;
    border: none;
    border-bottom: 1px solid $black;
    width: 100%;
    background-image: url(../img/search.svg);
    background-repeat: no-repeat;
    background-position: left center;
    padding: 0 60px 0 34px;
    font-size: 18px;
    &::placeholder{
      color: rgba($black, .4);
    }
  }
  &__btn{
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    font-size: 16px;
    text-transform: uppercase;
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 5.3px 0;
    &__inner{
      flex-direction: row;
      justify-content: space-between;
    }
    &__logo{
      width: 228px;
      margin-top: -9px;
    }
    &__burger{
      width: 24px;
      height: 24px;
      display: block;
      background-image: url(../img/burger.svg);
      background-repeat: no-repeat;
      background-position: center;
      transition: all .3s;
    }
    &__bottom,
    &__search{
      margin: 0;
    }
    &._open{
      .nav{
        transform: translateX(0);
      }
      .header__burger{
        background-image: url(../img/close.svg);
      }
    }
  }

  .nav{
    position: fixed;
    top: 70px;
    left: 0;
    width: 100%;
    height: calc(100vh - 70px);
    background-color: #fff;
    z-index: 10;
    padding: 24px;
    overflow-y: auto;
    transition: all .3s;
    transform: translateX(-120%);
    &__list{
      display: block;
    }
    &__show,
    &__link{
      padding: 13px;
      border-bottom: $border;
    }
    &__hidden{
      display: none;
      padding: 10px 0;
      box-shadow: none;
      position: static;
      opacity: 1;
      pointer-events: unset;
      transition: unset;
      .container{
        padding: 0 0 0 30px;
      }
      &-inner{
        display: block;
      }
    }
    &__links{
      display: block;
      padding: 0;
    }
    &__content{
      max-width: 400px;
    }
  }
}
