.blog-page{
  padding-bottom: 100px;
  &__items{
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 30px);
    margin-top: 29px;
  }
  .blog-card{
    width: calc(100% / 3 - 30px);
    margin: 30px 30px 0 0;
    &:nth-child(1),
    &:nth-child(2){
      width: calc(50% - 30px);
      margin-bottom: 50px;
    }
  }
}

.article{
  padding-bottom: 100px;
  &__content{
    margin-top: 74px;
    h3{
      font-size: 24px;
      line-height: 150%;
      letter-spacing: 0.1em;
      &:not(:last-child){
        margin-bottom: 24px;
      }
    }
    p{
      font-size: 16px;
      line-height: 175%;
      color: #5E5E5E;
      &:not(:last-child){
        margin-bottom: 24px;
      }
    }
    blockquote{
      padding: 40px 120px 45px;
      font-weight: 700;
      font-size: 16px;
      line-height: 175%;
      letter-spacing: 0.1em;
      text-align: center;
      border: 1px solid $black;
      &:not(:last-child){
        margin-bottom: 79px;
      }
    }
  }
  &__main{
    margin-bottom: 45px;
    min-height: 450px;
    @media screen and (min-width: 992px){
      p{
        &:not(:last-child){
          margin-bottom: 5px;
        }
      }
    }

    .article__img{
      width: calc(50% - 15px);
      margin: 0 !important;
      float: left;
      margin-right: 30px !important;
      clear: bottom;
    }
  }
  &__img{
    img{
      max-width: 932px;
      margin-left: auto;
      margin-right: auto;
      width: 100%;
      display: block;
    }
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }
  &__group{
    &:not(:last-child){
      margin-bottom: 60px;
    }
  }
}


@media screen and (max-width: 1150px){
  .article{
    &__main{
      min-height: 355px;
    }
  }
}


@media screen and (max-width: 992px){
  .blog-page{
    padding: 34px 0 20px;
    &__items{
      width: calc(100% + 20px);
      margin-top: 14px;
    }
    .blog-card{
      width: calc(100% / 3 - 20px);
      margin: 20px 20px 0 0;
      &:nth-child(1),
      &:nth-child(2){
        width: calc(50% - 20px);
        margin-bottom: 60px;
      }
    }
  }

  .article{
    padding: 34px 0 30px;
    &__content{
      margin-top: 22px;
      blockquote{
        padding: 28px 32px 34px;
        &:not(:last-child){
          margin-bottom: 30px;
        }
      }
      p{
        &:not(:last-child){
          margin-bottom: 15px;
        }
      }
      h3{
        font-size: 22px;
        &:not(:last-child){
          margin-bottom: 19px;
        }
      }
    }
    &__main{
      margin-bottom: 24px;
    }
    &__group{
      &:not(:last-child){
        margin-bottom: 40px;
      }
    }
  }
}


@media screen and (max-width: 740px){
  .blog-page{
    padding: 25px 0 21px;
    &__title{
      text-align: center;
    }
    &__items{
      width: 100%;
      margin: 0;
    }
    .blog-card{
      width: 100% !important;
      margin: 29px 0 0 !important;
      &:nth-child(2)  ~ .blog-card{
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
        .blog-card{
          &__title{
            margin: 0 0 10px;
          }
          &__content{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
          }
        }
      }
    }
  }

  .article{
    padding-top: 25px;
    &__title{
      font-size: 24px;
    }
    &__main{
      margin-bottom: 50px;
      .article__img{
        width: 100%;
        float: unset;
        margin: 0 0 22px !important;
      }
    }
    &__content{
      h3{
        &:not(:last-child){
          margin-bottom: 15px;
        }
      }
    }
    &__group{
      &:not(:last-child){
        margin-bottom: 30px;
      }
    }
  }
}
