.title{
  font-weight: 300;
  font-size: 48px;
  text-transform: uppercase;
}

.display1{
  font-size: 32px;
  line-height: 131%;
  letter-spacing: 0.1em;
}

.display2{
  font-size: 24px;
  line-height: 150%;
}

.body-text{
  letter-spacing: 0.1em;
  line-height: 150%;
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 10px;
    }
  }
  &._medium{
    font-size: 14px;
  }
  &._large{
    font-size: 18px;
  }
}

.line-text{
  font-weight: 300;
  display: flex;
  align-items: center;
  &:before{
    content: '';
    width: 36px;
    height: 1px;
    background-color: $black;
    margin-right: 11px;
  }
}

.gray-text{
  color: #5E5E5E;
}

.mlist{
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #000;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}


@media screen and (max-width: 576px){
  .title{
    font-size: 36px;
    line-height: 133%;
  }
}
