@import "../vars.scss";
.breadcrumbs {
    margin: 16px 0 40px;
    &__item {
        font-weight: 300;
        font-size: 14px;
        line-height: 150%;
        letter-spacing: 0.1em;
        display: inline;
        color: #5E5E5E;
        &:not(:first-child) {
            &:before {
                content: '>';
                padding: 0 5px;
            }
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        display: none;
    }
}
