.new-card{
  display: block;
  position: relative;
  overflow: hidden;
  padding-bottom: 43.8%;
  &:hover{
    .new-card__title{
      background: rgba(0, 0, 0, 0.8);
    }
  }
  img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__title{
    transition: all .3s;
    position: absolute;
    background: rgba(0, 0, 0, 0.6);
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    min-height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    line-height: 150%;
    letter-spacing: 0.1em;
    text-decoration-line: underline;
    color: #FFFFFF;
  }
}

.product-card{
  position: relative;
  &__badge{
    position: absolute;
    height: 38.36px;
    display: flex;
    align-items: center;
    padding: 0 23.55px;
    background-color: $black;
    color: #fff;
    font-size: 16px;
    white-space: nowrap;
    z-index: 2;
    top: 15px;
    left: 21px;
  }
  &__img{
    position: relative;
    padding-bottom: 150%;
    display: block;
    margin-bottom: 20px;
    img{
      position: absolute;
      top: 50%;
      left: 50%;
      max-width: 100%;
      max-height: 100%;
      transform: translate(-50%, -50%);
    }
  }
  &__price{
    font-weight: 500;
    font-family: $font2;
  }
  &__desc{
    color: #5E5E5E;
  }
  &__link{
    position: absolute;
    bottom: 16px;
    width: 100%;
    height: 50px;
    background: rgba(0, 0, 0, 0.7);
    z-index: 3;
    opacity: 0;
    transition: all .3s;
  }
  .swiper{
    &-container{
      overflow: hidden;
      position: relative;
      &:hover{
        .swiper-button-next,
        .swiper-button-prev{
          opacity: 1;
        }
      }
    }
    &-button{
      &-prev{
        left: 0;
        opacity: 0;
      }
      &-next{
        right: 0;
        opacity: 0;
      }
    }
  }
  &:hover{
    .product-card__link{
      opacity: 1;
    }
  }
}

.blog-card{
  &__img{
    display: block;
    position: relative;
    padding-bottom: 71.335%;
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title{
    margin: 18px 0 8px;
    display: block;
    text-transform: uppercase;
  }
}


@media screen and (max-width: 992px){
  .product-card{
    &__badge{
      top: 12px;
      left: 12px;
      padding: 0 25.26px;
      height: 36.52px;
    }
    &__img{
      margin-bottom: 6px;
    }
    .body-text{
      font-size: 14px;
    }
    &__title{
      text-transform: uppercase;
    }
  }

  .blog-card{
    &__title{
      margin: 13px 0 6px;
    }
  }
}


@media screen and (max-width: 576px){
  .product-card{
    &__badge{
      padding: 0 7px;
      top: 8px;
      left: 8px;
    }
    &__img{
      margin-bottom: 10px;
    }
    .body-text{
      font-size: 12px;
    }
    &__title{
      font-weight: 700;
      text-transform: unset;
    }
    &__price{
      font-weight: 700;
      font-family: $font1;
    }
  }
}
