.about-page{
  padding-bottom: 100px;
  &__img{
    display: block;
    width: 100%;
  }
  &__title{
    text-align: center;
    margin-top: 47px;
  }
  &__desc{
    margin: 41px auto 0;
    max-width: 630px;
    font-size: 16px;
    line-height: 175%;
    text-align: center;
  }
}

.about-detail{
  margin-top: 60px;
  min-height: 867px;
  &__img{
    width: 57.5%;
    float: left;
    margin: 0 60px 20px 0;
  }
  &__content{
    padding-top: 15.34%;
  }
  &__title{
    padding-top: 7%;
  }
  &__desc{
    margin-top: 30px;
    font-size: 14px;
    line-height: 214%;
    color: #5E5E5E;
  }
}

.about-quote{
  margin: 163px auto 0;
  width: 990px;
  max-width: 100%;
  border: 1px solid $black;
  padding: 74px 60px 56px;
  text-align: center;
  position: relative;
  &__decor{
    width: 225.29px;
    display: flex;
    justify-content: center;
    background-color: #fff;
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translate(-50%,10px);
  }
  &__title{
    font-weight: 700;
    font-size: 20px;
    line-height: 130%;
    letter-spacing: 0.1em;
    margin-bottom: 5px;
  }
  &__desc{
    max-width: 600px;
    margin: 0 auto;
    font-size: 16px;
    line-height: 28px;
  }
}


@media screen and (max-width: 1100px){
  .about-detail{
    min-height: 650px;
  }
}


@media screen and (max-width: 992px){
  .about-page{
    padding-bottom: 30px;
    &__img{
      width: calc(100% + 40px);
      margin: -1px 0 0 -20px;
    }
    &__title{
      margin-top: 45px;
    }
    &__desc{
      margin-top: 14px;
    }
  }

  .about-detail{
    margin-top: 75px;
    &__img{
      width: 51%;
      margin: 0 25px 0 0;
    }
    &__title{
      padding: 0;
    }
    &__desc{
      margin-top: 10px;
    }
  }

  .about-quote{
    padding: 53px 28px 28px;
    margin-top: 120px;
  }
}


@media screen and (max-width: 740px){
  .about-page{
    &__title{
      margin-top: 37px;
    }
    &__desc{
      margin-top: 24px;
    }
  }

  .about-detail{
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    &__img{
      order: 2;
      float: unset;
      margin: 22px 0 0 -20px;
      width: calc(100% + 40px);
    }
  }

  .about-quote{
    margin-top: 103px;
    padding: 58px 18px 40px;
  }
}
